.card-container{padding-left:0px;padding-right:0px}.card-container .card{box-shadow:0 15px 20px rgba(0,0,0,0.3);margin:10px}.card-container .card .card-block{padding:1rem}.card-container .card .card-block .card-title{font-size:1.1rem;white-space:nowrap;overflow:hidden;padding:0.3rem;position:relative}.card-container .card .card-block .card-title:after{content:'';position:absolute;right:0;top:0;width:40px;height:100%;background:-moz-linear-gradient(left, rgba(255,255,255,0.2), #fff 100%);background:-webkit-linear-gradient(left, rgba(255,255,255,0.2), #fff 100%);background:-o-linear-gradient(left, rgba(255,255,255,0.2), #fff 100%);background:-ms-linear-gradient(left, rgba(255,255,255,0.2), #fff 100%);background:linear-gradient(to right, rgba(255,255,255,0.2), #fff 100%)}.card-container .card .card-block .card-price{color:orangered;font-size:13pt;float:right}.card-container .card .card-block .card-text{white-space:nowrap;overflow:hidden}.card-container .card .card-block .card-text:after{content:'';position:absolute;right:0;width:120px;height:30px;background:-moz-linear-gradient(left, rgba(255,255,255,0.2), #fff 100%);background:-webkit-linear-gradient(left, rgba(255,255,255,0.2), #fff 100%);background:-o-linear-gradient(left, rgba(255,255,255,0.2), #fff 100%);background:-ms-linear-gradient(left, rgba(255,255,255,0.2), #fff 100%);background:linear-gradient(to right, rgba(255,255,255,0.2), #fff 100%)}.card-container .card .card-block .fig{text-align:center}.card-container .card .card-block img{height:150px;max-width:230px}.card-container{position:relative}.card-container .card:hover{position:absolute;z-index:1}.card-container .card:hover .card-title{font-size:1.1rem;white-space:normal;overflow:visible;padding:0.3rem}.card-container .card:hover .card-text{white-space:normal;overflow:visible}



